body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:hsl(210, 36%, 96%);
  color:hsl(209, 61%, 16%)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.section{
  padding: 5rem 0;
}
.section-center{
  width: 90vw;
  margin:0 auto;
  max-width: 35rem;
  margin-top: 8rem;
}
.section-center{
  background: #ffff;
  border-radius: 1rem;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s linear;
  padding: 2rem;
}
.form{
  display: flex;
  justify-content: center;

}
.alert-danger{
  color:red;
  background:#f8d7da;
}
.alert-success{
  color:green;
  background:#d4edda;
}
.edit-btn,
.delete-btn{
background:transparent ;
border-color: transparent;
cursor: pointer;
font-size: 1.0rem;
margin: 0 0.10rem;
transition: all 0.3s linear;
}
.edit-btn{
  color: lightgreen;
}
.delete-btn{
  color: red;
}